import { Component } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { finalize, share } from 'rxjs/operators';

import {
  MAT_MOMENT_DATE_FORMATS,
  MomentDateAdapter,
  MAT_MOMENT_DATE_ADAPTER_OPTIONS,
} from '@angular/material-moment-adapter';
import {DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE} from '@angular/material/core';

interface Matrix {additional: number[], destiny: number, lines: [string[]]}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  providers: [
    {provide: MAT_DATE_LOCALE, useValue: 'ru-RU'},
    {
      provide: DateAdapter,
      useClass: MomentDateAdapter,
      deps: [MAT_DATE_LOCALE, MAT_MOMENT_DATE_ADAPTER_OPTIONS]
    },
    {provide: MAT_DATE_FORMATS, useValue: {
      parse: {
        dateInput: 'DD.MM.YYYY',
      },
      display: {
          dateInput: 'DD.MM.YYYY',
          monthYearLabel: 'MMMM Y',
          dateA11yLabel: 'LL',
          monthYearA11yLabel: 'MMMM Y'
      }
    }},
  ],
})
export class AppComponent {
  constructor(private httpClient: HttpClient) {}

  title = 'client';

  loading = false;

  matrix: Matrix;

  date: Date;

  ngOnInit() {
  }

  calculate() {
    this.loading = true;
    this.httpClient.post('https://nenumerolog.herokuapp.com/api/matrix', {date: this.date})
      .pipe(
        finalize(() => {
            this.loading = false;
        }),
        share()
      )
      .subscribe(result => {
        this.matrix = result as Matrix;
      });
  }

  onDateChange(data) {
    this.date = data.value && Date.parse(data.value.toString()) ? data.value.utc(true) : '';
  }
}
